import React from 'react';
import { cn } from '../../utils';

/**
 * InfoRow Component
 * Row for displaying key-value information
 */
export const InfoRow = ({
  label,
  value,
  valueClassName = '',
  className = '',
  ...props
}) => {
  return (
    <div
      className={cn('flex justify-between items-center', className)}
      {...props}
    >
      <span className="text-gray-700 font-medium">{label}</span>
      <span className={cn('text-lg font-semibold text-gray-800', valueClassName)}>
        {value}
      </span>
    </div>
  );
};

